//КНОПКИ

.button {
  cursor: pointer;
  border: none;
}

.button--main {
  display: block;
  text-align: center;
  padding: 11px 0;
  background-color: @color-basic-green;
  font-family: @font-family-extra;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: @color-white;
}
